'use strict';
var React = require('react');
var ReactDOM = require('react-dom');
var Border = require('../common/Border');
var styleHelper = require('../../helper/styleHelper');
var measureHelper = require('../../helper/measureHelper');
var Dom = require('react-dom');
var LabelParts = require('../common/LabelParts');
var convertHelper = require('../../types/convertHelper');
var textHelper = require('../../helper/util').Text;
var appTypes = require('../../types/appTypes');

var Label = React.createClass({
  componentWillReceiveProps: function (nextProps) {
    //if (nextProps.text !== this.props.text) {
    this.isInvalid = true;
    //}
  },

  componentDidUpdate: function () {
    if (this.isInvalid) {
      this.isInvalid = false;
      this._doPaint();
    }
    this.doAdjustLinkArea();
  },

  _doPaint: function () {
    var com;
    var txDom;
    var parentDom;
    var w;
    var textHeight =styleHelper.builderLineHeight(this.props) + 1;
    com = Dom.findDOMNode(this);
    txDom = Dom.findDOMNode(this.refs.label);
    if (this.props.isAutoSize) {

      parentDom = com.parentElement;
      if (parentDom) {
        w = txDom.parentElement.clientWidth;
        $(parentDom).css({width:w,height:textHeight,lineHeight:textHeight + 'px'});
      }
    }
  },

  componentDidMount: function () {
    this._doPaint();
    $(ReactDOM.findDOMNode(this)).on(appTypes.CUSTOM_EVENT.ON_RESIZE,this.onResize);
  },

  onResize : function(e){
    this.doAdjustLinkArea();
  },

  doAdjustLinkArea : function(){
    var dom = ReactDOM.findDOMNode(this).parentElement;
    var w = dom.clientWidth;
    var h = dom.clientHeight;
    dom = $(dom);
    var linkArea = dom.children('.linkArea').first();
    var plink = linkArea.children('.page-link');
    var ilink = linkArea.children('.interactive-link');
    plink.css({width:w,height:h});
    ilink.css({width:w,height:h});
  },

  render: function () {
    var text = styleHelper.getText(this.props);
    var labelStyle;
    var bgColor = styleHelper.getBackgroundColor(this.props);
    var contextStyle = {
      backgroundColor: bgColor,
      pointerEvents:'none',
      position:'absolute'
    };
    if (!this.props.isAutoSize) {
      contextStyle.overflow = 'hidden';
    }
    text = textHelper.convertSpaceToNbsp(text);

    labelStyle = {
      pointerEvents: 'none',
      whiteSpace: 'nowrap'
    };
    styleHelper.adjustTextSizeStyle(labelStyle);
    styleHelper.StyleUtil(this.props,labelStyle).align().text();
    return (<div
      id={this.props.id}
      className="context"
      style={contextStyle}
    >
      <label ref="label"
        style={labelStyle}
      >{text}</label>
    </div>);
  }
});

module.exports = Label;



